
'use client';

import { useState, useEffect, Suspense } from 'react';
import { useSearchParams, useRouter } from 'next/navigation'; // Removed usePathname as it's not used
import Link from 'next/link';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Card, CardContent, CardDescription, CardHeader, CardTitle, CardFooter } from '@/components/ui/card';
import { Alert, AlertDescription, AlertTitle } from '@/components/ui/alert';
import { KeyRound, ShieldAlert, FilePenLine, Trophy, Loader2, ArrowLeftToLine, ShieldCheck, GanttChartSquare } from 'lucide-react';
import ManageWorks from '@/components/admin/ManageWorks';
import ManageCompetitions from '@/components/admin/ManageCompetitions';
import { fetchUserSessionForClient } from '@/app/actions';
import type { User } from '@/types';
import { cn } from '@/lib/utils';

type AdminView = 'works' | 'competitions' | 'reports';

interface AdminNavItem {
  key: AdminView;
  label: string;
  icon: React.ElementType;
}

const adminNavItems: AdminNavItem[] = [
  { key: 'works', label: '作品管理', icon: FilePenLine },
  { key: 'competitions', label: '比赛管理', icon: Trophy },
  { key: 'reports', label: '举报管理', icon: ShieldCheck },
];

function AdminSidebar({ activeView, setActiveView }: { activeView: AdminView, setActiveView: (view: AdminView) => void }) {
  const router = useRouter();

  const handleNav = (view: AdminView) => {
    setActiveView(view);
    router.push(`/admin?view=${view}`, { scroll: false });
  };

  return (
    <aside className="w-64 bg-card border-r border-sidebar-border flex-shrink-0 flex flex-col">
      <div className="p-4 border-b border-sidebar-border">
        <Button asChild variant="outline" className="w-full justify-start gap-2 text-sm hover:bg-sidebar-accent hover:text-sidebar-accent-foreground">
          <Link href="/">
            <ArrowLeftToLine className="h-4 w-4" />
            返回首页
          </Link>
        </Button>
      </div>
      <div className="p-4 border-b border-sidebar-border">
         <h2 className="text-lg font-semibold text-sidebar-foreground flex items-center">
            <GanttChartSquare className="mr-2 h-5 w-5 text-primary"/>
            管理菜单
         </h2>
      </div>
      <nav className="flex flex-col p-4 space-y-1 flex-grow">
        {adminNavItems.map((item) => (
          <Button
            key={item.key}
            variant={activeView === item.key ? 'secondary' : 'ghost'}
            className={cn(
              "w-full justify-start gap-2 text-sm",
              activeView === item.key ? "bg-sidebar-primary text-sidebar-primary-foreground hover:bg-sidebar-primary/90" : "hover:bg-sidebar-accent hover:text-sidebar-accent-foreground"
            )}
            onClick={() => handleNav(item.key)}
          >
            <item.icon className="h-4 w-4" />
            {item.label}
          </Button>
        ))}
      </nav>
       <div className="p-4 border-t border-sidebar-border mt-auto">
        <p className="text-xs text-muted-foreground">管理后台 v1.3</p>
      </div>
    </aside>
  );
}

function AdminPageContent() {
  const [password, setPassword] = useState('');
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [isLoadingSession, setIsLoadingSession] = useState(true);
  const [error, setError] = useState<string | null>(null);
  
  const searchParams = useSearchParams();
  const router = useRouter();
  const queryView = searchParams.get('view') as AdminView | null;
  
  const [activeView, setActiveView] = useState<AdminView>(
    (queryView && adminNavItems.some(item => item.key === queryView)) ? queryView : 'works'
  );

  useEffect(() => {
    const currentViewParam = searchParams.get('view') as AdminView | null;
    if (currentViewParam && adminNavItems.some(item => item.key === currentViewParam)) {
      if (activeView !== currentViewParam) { 
        setActiveView(currentViewParam);
      }
    } else {
      // If no valid view param, or current path is just /admin, default to 'works' and update URL
      if (!queryView || !adminNavItems.some(item => item.key === queryView)) {
         router.replace('/admin?view=works', { scroll: false });
         setActiveView('works');
      }
    }
  }, [searchParams, router, activeView, queryView]);


  useEffect(() => {
    async function checkAuth() {
      setIsLoadingSession(true);
      try {
        const sessionUser = await fetchUserSessionForClient();
        if (sessionUser && sessionUser.type === 'staff') {
          setIsAuthenticated(true);
          sessionStorage.setItem('adminAuthenticated', 'true'); 
        } else {
          const storedAuth = sessionStorage.getItem('adminAuthenticated');
          if (storedAuth === 'true') {
            setIsAuthenticated(true);
          } else {
            setIsAuthenticated(false); 
            sessionStorage.removeItem('adminAuthenticated'); 
          }
        }
      } catch (e) {
        console.error("Error fetching user session for admin page:", e);
        const storedAuth = sessionStorage.getItem('adminAuthenticated');
        setIsAuthenticated(storedAuth === 'true');
      } finally {
        setIsLoadingSession(false);
      }
    }
    checkAuth();
  }, []);


  const handleSubmit = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    if (password === '123456') {
      setIsAuthenticated(true);
      setError(null);
      sessionStorage.setItem('adminAuthenticated', 'true');
    } else {
      setError('密码错误，请重试。');
      setPassword('');
      sessionStorage.removeItem('adminAuthenticated');
    }
  };

  if (isLoadingSession) {
    return (
      <div className="flex flex-1 justify-center items-center h-full p-4 bg-background">
        <Loader2 className="h-12 w-12 animate-spin text-primary" />
        <p className="ml-3 text-lg text-muted-foreground">正在验证管理员身份...</p>
      </div>
    );
  }

  if (!isAuthenticated) {
    return (
      <div className="flex items-center justify-center flex-1 h-full bg-background p-4"> 
        <Card className="w-full max-w-md shadow-xl bg-card text-card-foreground border-border">
          <CardHeader className="text-center">
            <KeyRound className="mx-auto h-12 w-12 text-primary mb-3" />
            <CardTitle className="text-2xl font-bold">管理后台登录</CardTitle>
            <CardDescription>请输入密码以访问管理区域。</CardDescription>
          </CardHeader>
          <CardContent>
            <form onSubmit={handleSubmit} className="space-y-4">
              <Input
                type="password"
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                placeholder="请输入密码"
                className="text-center"
                aria-label="密码"
              />
              {error && (
                <Alert variant="destructive" className="text-sm border-destructive/50 bg-destructive/10 text-destructive [&>svg]:text-destructive">
                  <ShieldAlert className="h-4 w-4" />
                  <AlertTitle>登录失败</AlertTitle>
                  <AlertDescription>{error}</AlertDescription>
                </Alert>
              )}
              <Button type="submit" className="w-full bg-primary text-primary-foreground hover:bg-primary/90">
                登录
              </Button>
            </form>
             <Button variant="link" asChild className="w-full mt-4 text-sm">
                <Link href="/">返回首页</Link>
              </Button>
          </CardContent>
           <CardFooter className="text-xs text-muted-foreground pt-6 border-t mt-2">
            <p className="text-center w-full">
              <strong>安全提示:</strong> 此处为演示目的使用客户端密码校验。
            </p>
          </CardFooter>
        </Card>
      </div>
    );
  }

  return (
    <div className="flex flex-row flex-1 h-full bg-muted/40">
      <AdminSidebar activeView={activeView} setActiveView={setActiveView} />
      <main className="flex-1 p-4 sm:p-6 lg:p-8 overflow-y-auto bg-background">
         {activeView === 'works' && <ManageWorks initialFilterStatuses={['等待审核', '有效', '违规', '作弊', '无效']} />}
         {activeView === 'competitions' && <ManageCompetitions />}
         {activeView === 'reports' && <ManageWorks initialFilterStatuses={['审查中-被举报']} />}
      </main>
    </div>
  );
}


export default function AdminPage() {
  return (
    <Suspense fallback={
      <div className="flex flex-1 justify-center items-center h-screen bg-background">
        <Loader2 className="h-12 w-12 animate-spin text-primary" />
      </div>
    }>
      <AdminPageContent />
    </Suspense>
  );
}

