
'use client';

import { useEffect, useState, useMemo, useTransition } from 'react';
import { useForm } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import * as z from 'zod';
import type { Competition, CompetitionStatus } from '@/types';
import { getCompetitions, createCompetition, updateCompetition, deleteCompetition } from '@/app/actions';
import { Button } from '@/components/ui/button';
import {
  Dialog, DialogContent, DialogDescription, DialogFooter, DialogHeader, DialogTitle, DialogTrigger, DialogClose,
} from '@/components/ui/dialog';
import {
  AlertDialog,
  AlertDialogAction,
  AlertDialogCancel,
  AlertDialogContent,
  AlertDialogDescription,
  AlertDialogFooter,
  AlertDialogHeader,
  AlertDialogTitle,
} from "@/components/ui/alert-dialog";
import {
  Form, FormControl, FormField, FormItem, FormLabel, FormMessage, FormDescription
} from '@/components/ui/form';
import { Input } from '@/components/ui/input';
import { Textarea } from '@/components/ui/textarea';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow, TableCaption } from '@/components/ui/table';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Calendar } from '@/components/ui/calendar';
import { Popover, PopoverContent, PopoverTrigger } from '@/components/ui/popover';
import { useToast } from '@/hooks/use-toast';
import { Loader2, Edit, Trash2, PlusCircle, CalendarIcon as LucideCalendarIcon, ShieldAlert, RefreshCw, Trophy, Search, XCircle } from 'lucide-react';
import { cn } from '@/lib/utils';
import { format, parseISO, getYear } from 'date-fns';

const ALL_COMPETITION_STATUSES: CompetitionStatus[] = ['策划中', '进行中', '评审中', '已结束', '已归档'];
const ITEMS_PER_PAGE = 10;
const ALL_COMP_STATUSES_SELECT_VALUE = "__ALL_COMP_STATUSES__";
const ALL_YEARS_SELECT_VALUE = "__ALL_YEARS_MC__";


const competitionSchema = z.object({
  name: z.string().min(1, { message: '比赛名称不能为空。' }),
  year: z.coerce.number().min(2000, { message: '年份不能早于2000年。' }).max(2100, { message: '年份不能晚于2100年。' }),
  territoryPrefix: z.string()
    .min(3, { message: '领地前缀至少需要3个字符。' })
    .regex(/^\[[^\]]+\]$/, { message: '前缀必须以 "[" 开始并以 "]" 结束，例如：[24summer-builder]。' }),
  status: z.enum(ALL_COMPETITION_STATUSES, { required_error: '请选择比赛状态。' }),
  startDate: z.date({ required_error: '请选择开始日期。' }),
  endDate: z.date({ required_error: '请选择结束日期。' }),
  description: z.string().optional(),
}).refine(data => data.endDate >= data.startDate, {
  message: "结束日期不能早于开始日期。",
  path: ["endDate"],
});

type CompetitionFormValues = z.infer<typeof competitionSchema>;

export default function ManageCompetitions() {
  const [allCompetitions, setAllCompetitions] = useState<Competition[]>([]);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [isSubmitting, startSubmitTransition] = useTransition();
  const { toast } = useToast();

  const [isFormDialogOpen, setIsFormDialogOpen] = useState(false);
  const [editingCompetition, setEditingCompetition] = useState<Competition | null>(null);
  
  const [competitionToDelete, setCompetitionToDelete] = useState<Competition | null>(null);
  const [isDeleteConfirmOpen, setIsDeleteConfirmOpen] = useState(false);
  
  const [competitionToCloseOperation, setCompetitionToCloseOperation] = useState<Competition | null>(null);
  const [isCloseConfirmOpen, setIsCloseConfirmOpen] = useState(false);
  
  const [currentActioningId, setCurrentActioningId] = useState<string | null>(null);


  const [searchTerm, setSearchTerm] = useState('');
  const [statusFilter, setStatusFilter] = useState<string>(ALL_COMP_STATUSES_SELECT_VALUE);
  const [yearFilter, setYearFilter] = useState<string>(ALL_YEARS_SELECT_VALUE);
  const [currentPage, setCurrentPage] = useState(1);
  
  const uniqueYears = useMemo(() => {
    if (!allCompetitions) return [];
    const years = new Set(allCompetitions.map(comp => comp.year));
    return Array.from(years).sort((a, b) => b - a);
  }, [allCompetitions]);


  const form = useForm<CompetitionFormValues>({
    resolver: zodResolver(competitionSchema),
    defaultValues: {
      name: '',
      year: new Date().getFullYear(),
      territoryPrefix: `[${new Date().getFullYear().toString().slice(-2)}event-builder]`,
      status: '策划中',
      startDate: new Date(),
      endDate: new Date(new Date().setDate(new Date().getDate() + 30)),
      description: '',
    },
  });

  const fetchCompetitionsData = async () => {
    setIsLoading(true);
    setError(null);
    try {
      const data = await getCompetitions();
      setAllCompetitions(data);
    } catch (err) {
      console.error('Failed to fetch competitions:', err);
      setError('加载比赛数据失败，请稍后再试。');
      toast({ title: '错误', description: '加载比赛数据失败。', variant: 'destructive' });
    } finally {
      setIsLoading(false);
    }
  };

  useEffect(() => {
    fetchCompetitionsData();
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const filteredAndSearchedCompetitions = useMemo(() => {
    return allCompetitions
      .filter(comp => 
        (statusFilter === ALL_COMP_STATUSES_SELECT_VALUE || comp.status === statusFilter) &&
        (yearFilter === ALL_YEARS_SELECT_VALUE || comp.year === parseInt(yearFilter)) &&
        (!searchTerm || comp.name.toLowerCase().includes(searchTerm.toLowerCase()) || comp.territoryPrefix.toLowerCase().includes(searchTerm.toLowerCase()))
      ).sort((a,b) => b.year - a.year || new Date(b.startDate).getTime() - new Date(a.startDate).getTime());
  }, [allCompetitions, searchTerm, statusFilter, yearFilter]);

  const paginatedCompetitions = useMemo(() => {
    const startIndex = (currentPage - 1) * ITEMS_PER_PAGE;
    return filteredAndSearchedCompetitions.slice(startIndex, startIndex + ITEMS_PER_PAGE);
  }, [filteredAndSearchedCompetitions, currentPage]);

  const totalPages = Math.ceil(filteredAndSearchedCompetitions.length / ITEMS_PER_PAGE);

  const handleFormDialogOpen = (competition?: Competition | null) => {
    setEditingCompetition(competition || null);
    if (competition) {
      form.reset({
        name: competition.name,
        year: competition.year,
        territoryPrefix: competition.territoryPrefix,
        status: competition.status,
        startDate: parseISO(competition.startDate),
        endDate: parseISO(competition.endDate),
        description: competition.description || '',
      });
    } else {
      const currentYear = new Date().getFullYear();
      form.reset({
        name: '', year: currentYear, 
        territoryPrefix: `[${currentYear.toString().slice(-2)}event-builder]`,
        status: '策划中',
        startDate: new Date(), endDate: new Date(new Date().setDate(new Date().getDate() + 30)),
        description: '',
      });
    }
    setIsFormDialogOpen(true);
  };
  
  const handleFormDialogClose = () => {
    setIsFormDialogOpen(false); setEditingCompetition(null); form.reset();
  };

  const onFormSubmit = async (values: CompetitionFormValues) => {
    startSubmitTransition(async () => {
      setCurrentActioningId(editingCompetition ? editingCompetition.id : 'new');
      const apiData = {
        ...values,
        startDate: format(values.startDate, 'yyyy-MM-dd'),
        endDate: format(values.endDate, 'yyyy-MM-dd'),
        year: values.year, 
        territoryPrefix: values.territoryPrefix,
      };

      let result;
      if (editingCompetition) {
        result = await updateCompetition(editingCompetition.id, apiData);
      } else {
        const createData: Omit<Competition, 'id'> & { startDate: string; endDate: string; } = {
            name: apiData.name, year: apiData.year, status: apiData.status,
            startDate: apiData.startDate, endDate: apiData.endDate, description: apiData.description,
            territoryPrefix: apiData.territoryPrefix,
        };
        result = await createCompetition(createData);
      }

      if (result.success) {
        toast({ title: '成功', description: result.message });
        fetchCompetitionsData(); handleFormDialogClose();
      } else {
        toast({ title: '操作失败', description: result.message, variant: 'destructive' });
      }
      setCurrentActioningId(null);
    });
  };

  const handleDeleteButtonClick = (competition: Competition) => {
    setCompetitionToDelete(competition);
    setIsDeleteConfirmOpen(true);
  };

  const handleDeleteCompetitionConfirm = () => {
    if (!competitionToDelete) return;
    setCurrentActioningId(competitionToDelete.id);
    startSubmitTransition(async () => {
      const result = await deleteCompetition(competitionToDelete.id);
      if (result.success) {
        toast({ title: '删除成功', description: result.message });
        fetchCompetitionsData();
      } else {
        toast({ title: '删除失败', description: result.message, variant: 'destructive' });
      }
      setIsDeleteConfirmOpen(false);
      setCompetitionToDelete(null);
      setCurrentActioningId(null);
    });
  };

  const handleCloseCompetitionButtonClick = (competition: Competition) => {
    setCompetitionToCloseOperation(competition);
    setIsCloseConfirmOpen(true);
  };

  const handleCloseCompetitionConfirm = () => {
    if (!competitionToCloseOperation) return;
    setCurrentActioningId(competitionToCloseOperation.id);
    startSubmitTransition(async () => {
      const result = await updateCompetition(competitionToCloseOperation.id, { status: '已结束' });
      if (result.success) {
        toast({ title: '成功', description: `活动 "${competitionToCloseOperation.name}" 已关闭。` });
        fetchCompetitionsData();
      } else {
        toast({ title: '操作失败', description: result.message, variant: 'destructive' });
      }
      setIsCloseConfirmOpen(false);
      setCompetitionToCloseOperation(null);
      setCurrentActioningId(null);
    });
  };


  if (isLoading) return <div className="flex justify-center items-center py-10"><Loader2 className="h-12 w-12 animate-spin text-primary" /><p className="ml-3 text-lg">加载中...</p></div>;
  if (error) return <Card className="my-10 bg-destructive/10 border-destructive"><CardHeader><CardTitle className="text-destructive flex items-center"><ShieldAlert />错误</CardTitle></CardHeader><CardContent>{error}<Button onClick={fetchCompetitionsData} className="mt-2">重试</Button></CardContent></Card>;

  return (
    <Card className="shadow-lg mt-2">
      <CardHeader className="sm:flex sm:flex-row sm:items-center sm:justify-between gap-2">
        <div>
           <CardTitle className="text-2xl flex items-center"><Trophy className="mr-3 h-6 w-6 text-primary" />比赛列表</CardTitle>
          <CardDescription>创建、编辑和管理建筑比赛项目。</CardDescription>
        </div>
        <Dialog open={isFormDialogOpen} onOpenChange={ (open) => { if (!open) handleFormDialogClose(); else setIsFormDialogOpen(true); }}>
          <DialogTrigger asChild><Button onClick={() => handleFormDialogOpen(null)}><PlusCircle className="mr-2 h-5 w-5" />创建新比赛</Button></DialogTrigger>
          <DialogContent className="sm:max-w-2xl max-h-[90vh] overflow-y-auto p-6">
            <DialogHeader><DialogTitle>{editingCompetition ? '编辑比赛' : '创建新比赛'}</DialogTitle><DialogDescription>{editingCompetition ? '修改比赛的详细信息。' : '填写新比赛的详细信息。'}</DialogDescription></DialogHeader>
            <Form {...form}>
              <form onSubmit={form.handleSubmit(onFormSubmit)} className="space-y-5 mt-4">
                <FormField control={form.control} name="name" render={({ field }) => (<FormItem><FormLabel>比赛名称</FormLabel><FormControl><Input placeholder="例如：2024夏季建筑大赛" {...field} /></FormControl><FormMessage /></FormItem>)} />
                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                  <FormField control={form.control} name="year" render={({ field }) => (<FormItem><FormLabel>年份</FormLabel><FormControl><Input type="number" placeholder="例如：2024" {...field} /></FormControl><FormMessage /></FormItem>)} />
                  <FormField control={form.control} name="territoryPrefix" render={({ field }) => (<FormItem><FormLabel>领地前缀</FormLabel><FormControl><Input placeholder="例如：[24summer-builder]" {...field} /></FormControl><FormDescription>用于领地名称，必须以 '[' 开始，以 ']' 结束。</FormDescription><FormMessage /></FormItem>)} />
                </div>
                <FormField control={form.control} name="status" render={({ field }) => (<FormItem><FormLabel>状态</FormLabel><Select onValueChange={field.onChange} defaultValue={field.value}><FormControl><SelectTrigger><SelectValue placeholder="选择比赛状态" /></SelectTrigger></FormControl><SelectContent>{ALL_COMPETITION_STATUSES.map(s => (<SelectItem key={s} value={s}>{s}</SelectItem>))}</SelectContent></Select><FormMessage /></FormItem>)} />
                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                  <FormField control={form.control} name="startDate" render={({ field }) => (<FormItem className="flex flex-col"><FormLabel>开始日期</FormLabel><Popover><PopoverTrigger asChild><FormControl><Button variant={"outline"} className={cn("w-full justify-start text-left font-normal",!field.value && "text-muted-foreground")}><LucideCalendarIcon className="mr-2 h-4 w-4" />{field.value ? format(field.value, "PPP") : <span>选择日期</span>}</Button></FormControl></PopoverTrigger><PopoverContent className="w-auto p-0" align="start"><Calendar mode="single" selected={field.value} onSelect={field.onChange} disabled={(date) => date < new Date("2000-01-01")} initialFocus /></PopoverContent></Popover><FormMessage /></FormItem>)} />
                  <FormField control={form.control} name="endDate" render={({ field }) => (<FormItem className="flex flex-col"><FormLabel>结束日期</FormLabel><Popover><PopoverTrigger asChild><FormControl><Button variant={"outline"} className={cn("w-full justify-start text-left font-normal",!field.value && "text-muted-foreground")}><LucideCalendarIcon className="mr-2 h-4 w-4" />{field.value ? format(field.value, "PPP") : <span>选择日期</span>}</Button></FormControl></PopoverTrigger><PopoverContent className="w-auto p-0" align="start"><Calendar mode="single" selected={field.value} onSelect={field.onChange} disabled={(date) => date < (form.getValues("startDate") || new Date("2000-01-01"))} initialFocus /></PopoverContent></Popover><FormMessage /></FormItem>)} />
                </div>
                <FormField control={form.control} name="description" render={({ field }) => (<FormItem><FormLabel>描述 <span className="text-muted-foreground text-xs">(可选)</span></FormLabel><FormControl><Textarea placeholder="输入比赛的简要描述和规则..." {...field} rows={3} /></FormControl><FormMessage /></FormItem>)} />
                <DialogFooter className="pt-4"><DialogClose asChild><Button type="button" variant="outline" onClick={handleFormDialogClose}>取消</Button></DialogClose><Button type="submit" disabled={isSubmitting}>{isSubmitting && <Loader2 className="mr-2 h-4 w-4 animate-spin" />}{editingCompetition ? '保存更改' : '创建比赛'}</Button></DialogFooter>
              </form>
            </Form>
          </DialogContent>
        </Dialog>
      </CardHeader>
      <CardContent>
         <div className="my-4 flex flex-col sm:flex-row gap-4">
          <div className="relative flex-grow">
            <Search className="absolute left-2.5 top-1/2 -translate-y-1/2 h-4 w-4 text-muted-foreground" />
            <Input type="search" placeholder="搜索比赛名称、前缀..." value={searchTerm} onChange={(e) => { setSearchTerm(e.target.value); setCurrentPage(1);}} className="pl-8 w-full" />
          </div>
          <Select 
            value={statusFilter} 
            onValueChange={(value) => { 
              setStatusFilter(value === ALL_COMP_STATUSES_SELECT_VALUE ? ALL_COMP_STATUSES_SELECT_VALUE : value); 
              setCurrentPage(1); 
            }}
          >
            <SelectTrigger className="w-full sm:w-[180px]"><SelectValue placeholder="筛选状态" /></SelectTrigger>
            <SelectContent>
              <SelectItem value={ALL_COMP_STATUSES_SELECT_VALUE}>所有状态</SelectItem>
              {ALL_COMPETITION_STATUSES.map(s => <SelectItem key={s} value={s}>{s}</SelectItem>)}
            </SelectContent>
          </Select>
          {uniqueYears.length > 0 && (
            <Select 
              value={yearFilter} 
              onValueChange={(value) => { 
                setYearFilter(value === ALL_YEARS_SELECT_VALUE ? ALL_YEARS_SELECT_VALUE : value); 
                setCurrentPage(1); 
              }}
            >
              <SelectTrigger className="w-full sm:w-[120px]"><SelectValue placeholder="筛选年份" /></SelectTrigger>
              <SelectContent>
                <SelectItem value={ALL_YEARS_SELECT_VALUE}>所有年份</SelectItem>
                {uniqueYears.map(y => <SelectItem key={y} value={String(y)}>{y}年</SelectItem>)}
              </SelectContent>
            </Select>
          )}
        </div>

        {paginatedCompetitions.length === 0 ? (
          <p className="text-center text-muted-foreground py-6">{filteredAndSearchedCompetitions.length === 0 && (searchTerm || statusFilter !== ALL_COMP_STATUSES_SELECT_VALUE || yearFilter !== ALL_YEARS_SELECT_VALUE) ? '没有匹配当前筛选条件的比赛。' : '暂无比赛记录。'}</p>
        ) : (
          <div className="overflow-x-auto">
            <Table>
              <TableCaption>共 {filteredAndSearchedCompetitions.length} 个比赛记录，当前显示第 {currentPage} / {totalPages} 页。</TableCaption>
              <TableHeader>
                <TableRow>
                  <TableHead className="min-w-[200px] w-[25%]">比赛名称</TableHead>
                  <TableHead className="w-[10%]">年份</TableHead>
                  <TableHead className="min-w-[150px] w-[15%]">领地前缀</TableHead>
                  <TableHead className="w-[10%]">状态</TableHead>
                  <TableHead className="w-[10%]">开始日期</TableHead><TableHead className="w-[10%]">结束日期</TableHead>
                  <TableHead className="text-right w-[20%] min-w-[180px]">操作</TableHead>
                </TableRow>
              </TableHeader>
              <TableBody>
                {paginatedCompetitions.map(comp => (
                  <TableRow key={comp.id}>
                    <TableCell className="font-medium">{comp.name}</TableCell>
                    <TableCell>{comp.year}</TableCell>
                    <TableCell className="font-mono text-xs">{comp.territoryPrefix}</TableCell>
                    <TableCell><span className={cn("px-2 py-1 rounded-full text-xs font-semibold whitespace-nowrap", comp.status === '进行中' && 'bg-blue-500/20 text-blue-400 border-blue-500/30', comp.status === '策划中' && 'bg-yellow-500/20 text-yellow-400 border-yellow-500/30', comp.status === '评审中' && 'bg-purple-500/20 text-purple-400 border-purple-500/30', comp.status === '已结束' && 'bg-green-500/20 text-green-400 border-green-500/30', comp.status === '已归档' && 'bg-gray-500/20 text-gray-400 border-gray-500/30')}>{comp.status}</span></TableCell>
                    <TableCell>{format(parseISO(comp.startDate), "yyyy-MM-dd")}</TableCell>
                    <TableCell>{format(parseISO(comp.endDate), "yyyy-MM-dd")}</TableCell>
                    <TableCell className="text-right space-x-1">
                      <Button variant="ghost" size="icon" onClick={() => handleFormDialogOpen(comp)} className="mr-1" title="编辑"><Edit className="h-4 w-4" /></Button>
                      {(comp.status === '进行中' || comp.status === '策划中' || comp.status === '评审中') && (
                          <Button 
                            variant="ghost" 
                            size="icon" 
                            className="text-destructive hover:bg-destructive/10 hover:text-destructive"
                            onClick={() => handleCloseCompetitionButtonClick(comp)} 
                            disabled={isSubmitting && currentActioningId === comp.id}
                            title="关闭活动"
                          >
                            {isSubmitting && currentActioningId === comp.id ? <Loader2 className="h-4 w-4 animate-spin"/> : <XCircle className="h-4 w-4" />}
                          </Button>
                      )}
                        <Button 
                            variant="ghost" 
                            size="icon" 
                            className="bg-destructive/80 text-destructive-foreground hover:bg-destructive hover:text-destructive-foreground"
                            onClick={() => handleDeleteButtonClick(comp)} 
                            disabled={isSubmitting && currentActioningId === comp.id}
                            title="删除比赛"
                          >
                            {isSubmitting && currentActioningId === comp.id ? <Loader2 className="h-4 w-4 animate-spin"/> : <Trash2 className="h-4 w-4" />}
                          </Button>
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </div>
        )}
        {totalPages > 1 && (
          <div className="flex justify-center items-center space-x-2 pt-6">
            <Button onClick={() => setCurrentPage(p => Math.max(1, p - 1))} disabled={currentPage === 1} size="sm">上一页</Button>
            <span className="text-sm text-muted-foreground">第 {currentPage} / {totalPages} 页</span>
            <Button onClick={() => setCurrentPage(p => Math.min(totalPages, p + 1))} disabled={currentPage === totalPages} size="sm">下一页</Button>
          </div>
        )}
      </CardContent>

      {/* Delete Competition Dialog */}
      <AlertDialog open={isDeleteConfirmOpen} onOpenChange={(open) => { if(!open) setCompetitionToDelete(null); setIsDeleteConfirmOpen(open);}}>
        <AlertDialogContent>
          {competitionToDelete && (
            <>
              <AlertDialogHeader>
                <AlertDialogTitle>确认删除比赛?</AlertDialogTitle>
                <AlertDialogDescription>
                  您确定要删除比赛 “{competitionToDelete.name}” ({competitionToDelete.year}) 吗？此操作无法撤销，与此比赛关联的所有作品及其投票也将被一并删除。
                </AlertDialogDescription>
              </AlertDialogHeader>
              <AlertDialogFooter>
                <AlertDialogCancel onClick={() => { setIsDeleteConfirmOpen(false); setCompetitionToDelete(null); }}>取消</AlertDialogCancel>
                <AlertDialogAction 
                  onClick={handleDeleteCompetitionConfirm} 
                  disabled={isSubmitting && currentActioningId === competitionToDelete.id}
                  className={cn("bg-destructive hover:bg-destructive/90 text-destructive-foreground", isSubmitting && currentActioningId === competitionToDelete.id && "opacity-50 cursor-not-allowed")}
                >
                  {isSubmitting && currentActioningId === competitionToDelete.id ? <Loader2 className="mr-2 h-4 w-4 animate-spin" /> : null}
                  确认删除
                </AlertDialogAction>
              </AlertDialogFooter>
            </>
          )}
        </AlertDialogContent>
      </AlertDialog>

      {/* Close Competition Dialog */}
      <AlertDialog open={isCloseConfirmOpen} onOpenChange={(open) => { if(!open) setCompetitionToCloseOperation(null); setIsCloseConfirmOpen(open);}}>
        <AlertDialogContent>
          {competitionToCloseOperation && (
            <>
              <AlertDialogHeader>
                <AlertDialogTitle>确认关闭活动?</AlertDialogTitle>
                <AlertDialogDescription>
                  您确定要关闭活动 “{competitionToCloseOperation.name}” ({competitionToCloseOperation.year}) 吗？活动状态将被设置为“已结束”。
                </AlertDialogDescription>
              </AlertDialogHeader>
              <AlertDialogFooter>
                <AlertDialogCancel onClick={() => { setIsCloseConfirmOpen(false); setCompetitionToCloseOperation(null); }}>取消</AlertDialogCancel>
                <AlertDialogAction 
                  onClick={handleCloseCompetitionConfirm} 
                  disabled={isSubmitting && currentActioningId === competitionToCloseOperation.id}
                  className={cn("bg-destructive hover:bg-destructive/90 text-destructive-foreground", isSubmitting && currentActioningId === competitionToCloseOperation.id && "opacity-50 cursor-not-allowed")}
                >
                  {isSubmitting && currentActioningId === competitionToCloseOperation.id ? <Loader2 className="mr-2 h-4 w-4 animate-spin" /> : null}
                  确认关闭
                </AlertDialogAction>
              </AlertDialogFooter>
            </>
          )}
        </AlertDialogContent>
      </AlertDialog>
    </Card>
  );
}
