
'use client';

import { useEffect, useState, useMemo, useTransition } from 'react';
import type { Building, BuildingStatus } from '@/types';
import { getRawBuildings, updateBuildingStatus, dismissReportAction, deleteBuildingAction } from '@/app/actions';
import { Button } from '@/components/ui/button';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow, TableCaption } from '@/components/ui/table';
import { Input } from '@/components/ui/input';
import { useToast } from '@/hooks/use-toast';
import { Loader2, RefreshCw, ShieldAlert, Edit as EditIcon, Search, Trash2, Info, Ban, ShieldCheck } from 'lucide-react';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '../ui/card';
import { format, parseISO } from 'date-fns';
import { Tooltip, TooltipContent, TooltipProvider, TooltipTrigger } from '@/components/ui/tooltip';
import { cn } from '@/lib/utils';
import {
  AlertDialog,
  AlertDialogAction,
  AlertDialogCancel,
  AlertDialogContent,
  AlertDialogDescription,
  AlertDialogFooter,
  AlertDialogHeader,
  AlertDialogTitle,
} from "@/components/ui/alert-dialog";


const ALL_DEFINED_STATUSES: BuildingStatus[] = ['有效', '等待审核', '违规', '作弊', '无效', '审查中-被举报'];
const ITEMS_PER_PAGE = 10;
const ALL_STATUSES_SELECT_VALUE = "__ALL_STATUSES_MW__";

interface ManageWorksProps {
  initialFilterStatuses?: BuildingStatus[];
}

export default function ManageWorks({ initialFilterStatuses }: ManageWorksProps) {
  const [allBuildings, setAllBuildings] = useState<Building[]>([]);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [isUpdating, startUpdateTransition] = useTransition();
  const { toast } = useToast();

  const [searchTerm, setSearchTerm] = useState('');
  
  const [statusFilter, setStatusFilter] = useState<string>(() => {
    if (initialFilterStatuses && initialFilterStatuses.length === 1) {
      return initialFilterStatuses[0];
    }
    return ALL_STATUSES_SELECT_VALUE;
  });

  const [currentPage, setCurrentPage] = useState(1);
  
  const [buildingToDelete, setBuildingToDelete] = useState<Building | null>(null);
  const [isDeleteDialogOpen, setIsDeleteDialogOpen] = useState(false);
  const [currentActioningId, setCurrentActioningId] = useState<string | null>(null); // Combined for delete/dismiss


  const fetchBuildingsData = async () => {
    setIsLoading(true);
    setError(null);
    try {
      const data = await getRawBuildings();
      setAllBuildings(data);
      // Reset statusFilter if initialFilterStatuses changes
      if (initialFilterStatuses && initialFilterStatuses.length === 1 && statusFilter !== initialFilterStatuses[0]) {
        setStatusFilter(initialFilterStatuses[0]);
      } else if (initialFilterStatuses && initialFilterStatuses.length > 1 && statusFilter !== ALL_STATUSES_SELECT_VALUE) {
        setStatusFilter(ALL_STATUSES_SELECT_VALUE);
      } else if (!initialFilterStatuses && statusFilter !== ALL_STATUSES_SELECT_VALUE) {
         // If no initial filters, but some filter was previously set, keep it or reset to ALL
      }

    } catch (err) {
      console.error('Failed to fetch buildings:', err);
      setError('加载作品数据失败，请稍后再试。');
      toast({ title: '错误', description: '加载作品数据失败。', variant: 'destructive' });
    } finally {
      setIsLoading(false);
    }
  };
  
  useEffect(() => {
    fetchBuildingsData();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [JSON.stringify(initialFilterStatuses)]); // Re-fetch if initialFilterStatuses prop changes
  
  const availableStatusFilters = useMemo(() => {
    if (initialFilterStatuses && initialFilterStatuses.length > 0) {
        return initialFilterStatuses;
    }
    return ALL_DEFINED_STATUSES;
  }, [initialFilterStatuses]);


  const filteredAndSearchedBuildings = useMemo(() => {
    let worksToProcess = allBuildings;

    // Phase 1: Filter by initialFilterStatuses if they are defined (these are the "base" items for this view)
    if (initialFilterStatuses && initialFilterStatuses.length > 0) {
        worksToProcess = allBuildings.filter(b => initialFilterStatuses.includes(b.status));
    }

    // Phase 2: Apply dropdown filter. If "ALL" is selected in dropdown, it means show all from `worksToProcess`.
    // Otherwise, filter `worksToProcess` by the specific status selected in the dropdown.
    if (statusFilter !== ALL_STATUSES_SELECT_VALUE) {
        worksToProcess = worksToProcess.filter(b => b.status === statusFilter);
    }

    // Phase 3: Apply search term
    if (searchTerm) {
      const lowerSearchTerm = searchTerm.toLowerCase();
      worksToProcess = worksToProcess.filter(b =>
        b.name.toLowerCase().includes(lowerSearchTerm) ||
        b.author.toLowerCase().includes(lowerSearchTerm) ||
        (b.reportReason && b.reportReason.toLowerCase().includes(lowerSearchTerm))
      );
    }
    return worksToProcess.sort((a, b) => (new Date(b.submissionDate || 0).getTime()) - (new Date(a.submissionDate || 0).getTime()));
  }, [allBuildings, searchTerm, statusFilter, initialFilterStatuses]);


  const paginatedBuildings = useMemo(() => {
    const startIndex = (currentPage - 1) * ITEMS_PER_PAGE;
    return filteredAndSearchedBuildings.slice(startIndex, startIndex + ITEMS_PER_PAGE);
  }, [filteredAndSearchedBuildings, currentPage]);

  const totalPages = Math.ceil(filteredAndSearchedBuildings.length / ITEMS_PER_PAGE);

  const handleStatusChange = (buildingId: string, newStatus: BuildingStatus) => {
    setCurrentActioningId(buildingId);
    startUpdateTransition(async () => {
      const result = await updateBuildingStatus(buildingId, newStatus);
      if (result.success) {
        toast({ title: '成功', description: result.message });
        fetchBuildingsData(); 
      } else {
        toast({ title: '更新失败', description: result.message, variant: 'destructive' });
      }
      setCurrentActioningId(null);
    });
  };

  const handleDismissReportButtonClick = (building: Building) => {
    setBuildingToDelete(building); // Re-use buildingToDelete for context, action is different
    // No separate dialog state, just confirm
     if (window.confirm(`确定要撤销对作品 "${building.name}" 的举报吗？作品状态将恢复为“有效”。`)) {
      setCurrentActioningId(building.id);
      startUpdateTransition(async () => {
        const result = await dismissReportAction(building.id);
        if (result.success) {
          toast({ title: '成功', description: result.message });
          fetchBuildingsData();
        } else {
          toast({ title: '操作失败', description: result.message, variant: 'destructive' });
        }
        setCurrentActioningId(null);
        setBuildingToDelete(null);
      });
    } else {
      setBuildingToDelete(null);
    }
  };

  const handleDeleteButtonClick = (building: Building) => {
    setBuildingToDelete(building);
    setIsDeleteDialogOpen(true);
  };

  const handleDeleteConfirm = () => {
    if (!buildingToDelete) return;
    setCurrentActioningId(buildingToDelete.id);
    startUpdateTransition(async () => {
      const result = await deleteBuildingAction(buildingToDelete.id);
      if (result.success) {
        toast({ title: '删除成功', description: result.message });
        fetchBuildingsData();
      } else {
        toast({ title: '删除失败', description: result.message, variant: 'destructive' });
      }
      setIsDeleteDialogOpen(false);
      setBuildingToDelete(null);
      setCurrentActioningId(null);
    });
  };
  
  const getStatusBadgeClass = (status: BuildingStatus) => {
    switch (status) {
      case '有效': return 'bg-green-500/20 text-green-400 border-green-500/30';
      case '等待审核': return 'bg-yellow-500/20 text-yellow-400 border-yellow-500/30';
      case '审查中-被举报': return 'bg-orange-500/20 text-orange-400 border-orange-500/30';
      case '违规': case '作弊': case '无效': return 'bg-red-500/20 text-red-400 border-red-500/30';
      default: return 'bg-muted text-muted-foreground';
    }
  };


  if (isLoading) {
    return (
      <div className="flex justify-center items-center py-10">
        <Loader2 className="h-12 w-12 animate-spin text-primary" />
        <p className="ml-3 text-lg">正在加载作品数据...</p>
      </div>
    );
  }

  if (error) {
    return (
      <Card className="my-10 bg-destructive/10 border-destructive">
        <CardHeader><CardTitle className="flex items-center text-destructive"><ShieldAlert className="mr-2 h-6 w-6" />加载错误</CardTitle></CardHeader>
        <CardContent>
          <p className="text-destructive-foreground">{error}</p>
          <Button onClick={fetchBuildingsData} variant="destructive" className="mt-4"><RefreshCw className="mr-2 h-4 w-4" />重试</Button>
        </CardContent>
      </Card>
    );
  }

  return (
    <TooltipProvider>
      <Card className="mt-2 shadow-lg">
        <CardHeader>
          <CardTitle className="text-2xl flex items-center">
              {initialFilterStatuses?.includes('审查中-被举报') ? <ShieldCheck className="mr-3 h-6 w-6 text-orange-500" /> : <EditIcon className="mr-3 h-6 w-6 text-primary" />}
              {initialFilterStatuses?.includes('审查中-被举报') ? '举报受理' : '作品管理'}
          </CardTitle>
          <CardDescription>{initialFilterStatuses?.includes('审查中-被举报') ? '处理被举报的作品。' : '查看和修改已提交建筑作品的审核状态。'}</CardDescription>
          <div className="mt-4 flex flex-col sm:flex-row gap-4">
            <div className="relative flex-grow">
              <Search className="absolute left-2.5 top-1/2 -translate-y-1/2 h-4 w-4 text-muted-foreground" />
              <Input
                type="search"
                placeholder="搜索作品名、作者、举报原因..."
                value={searchTerm}
                onChange={(e) => { setSearchTerm(e.target.value); setCurrentPage(1); }}
                className="pl-8 w-full"
              />
            </div>
            <Select 
              value={statusFilter} 
              onValueChange={(value) => { 
                setStatusFilter(value); 
                setCurrentPage(1); 
              }}
            >
              <SelectTrigger className="w-full sm:w-[180px]">
                <SelectValue placeholder="筛选状态" />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value={ALL_STATUSES_SELECT_VALUE}>
                  {initialFilterStatuses && initialFilterStatuses.length > 0 ? '所有相关状态' : '所有状态'}
                </SelectItem>
                {availableStatusFilters.map(s => <SelectItem key={s} value={s}>{s}</SelectItem>)}
              </SelectContent>
            </Select>
          </div>
        </CardHeader>
        <CardContent>
          {paginatedBuildings.length === 0 ? (
            <p className="text-center text-muted-foreground py-6">
              {filteredAndSearchedBuildings.length === 0 && (searchTerm || statusFilter !== ALL_STATUSES_SELECT_VALUE) ? '没有匹配当前筛选条件的作品。' : '暂无作品记录。'}
            </p>
          ) : (
            <div className="overflow-x-auto">
              <Table>
                <TableCaption>共 {filteredAndSearchedBuildings.length} 个作品，当前显示第 {currentPage} / {totalPages} 页。</TableCaption>
                <TableHeader>
                  <TableRow>
                    <TableHead className="min-w-[150px]">作品名称</TableHead>
                    <TableHead className="min-w-[100px]">作者</TableHead>
                    <TableHead className="min-w-[120px]">当前状态</TableHead>
                    <TableHead className="min-w-[200px]">举报信息</TableHead>
                    <TableHead className="min-w-[280px] text-center">操作</TableHead>
                  </TableRow>
                </TableHeader>
                <TableBody>
                  {paginatedBuildings.map(building => (
                    <TableRow key={building.id}>
                      <TableCell className="font-medium">{building.name}</TableCell>
                      <TableCell>{building.author}</TableCell>
                      <TableCell>
                        <span className={cn("px-2 py-1 rounded-full text-xs font-semibold whitespace-nowrap", getStatusBadgeClass(building.status))}>
                          {building.status}
                        </span>
                      </TableCell>
                      <TableCell>
                        {building.reportReason ? (
                           <Tooltip delayDuration={100}>
                            <TooltipTrigger asChild>
                              <div className="flex items-center gap-1 text-sm cursor-default">
                                <Info className="h-4 w-4 text-orange-500 shrink-0" /> 
                                <p className="truncate max-w-[150px]">{building.reportReason}</p>
                              </div>
                            </TooltipTrigger>
                            <TooltipContent side="top" align="start" className="max-w-xs bg-card border-border p-2 rounded-md shadow-lg z-50">
                              <p className="font-semibold text-card-foreground">举报原因:</p>
                              <p className="text-xs text-muted-foreground whitespace-pre-wrap break-words">{building.reportReason}</p>
                              {building.lastReportedBy && <p className="text-xs mt-1">举报者ID: {building.lastReportedBy}</p>}
                              {building.lastReportDate && <p className="text-xs">举报于: {format(parseISO(building.lastReportDate), "yyyy-MM-dd HH:mm")}</p>}
                            </TooltipContent>
                          </Tooltip>
                        ) : (
                          <span className="text-xs text-muted-foreground">无</span>
                        )}
                      </TableCell>
                      <TableCell className="text-center">
                        <div className="flex items-center justify-center space-x-1">
                          <Select
                            defaultValue={building.status}
                            onValueChange={(newStatus) => handleStatusChange(building.id, newStatus as BuildingStatus)}
                            disabled={isUpdating && currentActioningId === building.id}
                          >
                            <SelectTrigger className="w-[130px] h-8 text-xs">
                              <SelectValue placeholder="修改状态" />
                            </SelectTrigger>
                            <SelectContent>
                              {ALL_DEFINED_STATUSES.map(statusValue => (
                                <SelectItem key={statusValue} value={statusValue} className="text-xs">
                                  {statusValue}
                                </SelectItem>
                              ))}
                            </SelectContent>
                          </Select>
                          {building.status === '审查中-被举报' && (
                            <Button
                              variant="ghost"
                              size="sm"
                              className="h-8 px-2 text-xs text-green-500 hover:bg-green-500/10 hover:text-green-600"
                              onClick={() => handleDismissReportButtonClick(building)}
                              disabled={isUpdating && currentActioningId === building.id}
                              title="撤销举报并将状态设为有效"
                            >
                              {isUpdating && currentActioningId === building.id ? <Loader2 className="mr-1 h-3 w-3 animate-spin"/> : <Ban className="mr-1 h-3 w-3" />} 
                              撤销举报
                            </Button>
                          )}
                          <Button
                              variant="ghost"
                              size="sm"
                              className="h-8 px-2 text-xs bg-destructive/80 text-destructive-foreground hover:bg-destructive hover:text-destructive-foreground"
                              onClick={() => handleDeleteButtonClick(building)}
                              disabled={isUpdating && currentActioningId === building.id}
                              title="删除作品"
                            >
                              {isUpdating && currentActioningId === building.id ? <Loader2 className="mr-1 h-3 w-3 animate-spin" /> : <Trash2 className="mr-1 h-3 w-3" />}
                               删除
                            </Button>
                        </div>
                      </TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </div>
          )}
          {totalPages > 1 && (
            <div className="flex justify-center items-center space-x-2 pt-6">
              <Button onClick={() => setCurrentPage(p => Math.max(1, p - 1))} disabled={currentPage === 1} size="sm">上一页</Button>
              <span className="text-sm text-muted-foreground">第 {currentPage} / {totalPages} 页</span>
              <Button onClick={() => setCurrentPage(p => Math.min(totalPages, p + 1))} disabled={currentPage === totalPages} size="sm">下一页</Button>
            </div>
          )}
        </CardContent>
      </Card>

      <AlertDialog open={isDeleteDialogOpen} onOpenChange={setIsDeleteDialogOpen}>
        <AlertDialogContent>
          {buildingToDelete && (
            <>
              <AlertDialogHeader>
                <AlertDialogTitle>确认删除作品?</AlertDialogTitle>
                <AlertDialogDescription>
                  您确定要删除作品 “{buildingToDelete.name}” 吗？此操作无法撤销。
                </AlertDialogDescription>
              </AlertDialogHeader>
              <AlertDialogFooter>
                <AlertDialogCancel onClick={() => { setIsDeleteDialogOpen(false); setBuildingToDelete(null);}}>取消</AlertDialogCancel>
                <AlertDialogAction 
                  onClick={handleDeleteConfirm} 
                  disabled={isUpdating && currentActioningId === buildingToDelete.id}
                  className={cn("bg-destructive hover:bg-destructive/90 text-destructive-foreground", isUpdating && currentActioningId === buildingToDelete.id && "opacity-50 cursor-not-allowed")}
                >
                  {isUpdating && currentActioningId === buildingToDelete.id ? <Loader2 className="mr-2 h-4 w-4 animate-spin" /> : null}
                  确认删除
                </AlertDialogAction>
              </AlertDialogFooter>
            </>
          )}
        </AlertDialogContent>
      </AlertDialog>
    </TooltipProvider>
  );
}
